.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #000;
}

.header img {
  width: 150px;
  height: auto;
}

.header h2 {
  color: white;
  text-align: center;
  width: 100%;
  font-size: 2.5rem;
}
@media screen and (max-width: 991px) {
  .header h2 {
    font-size: 1.5rem;
  }
}

.memContent {
  position: relative;
}

.memContent img {
  width: 100%;
  height: auto;
}

.memContent h2 {
  font-family: impact;
  font-size: 2.5rem;
  text-transform: uppercase;
  text-shadow:2px 2px 0 #000,
  -2px -2px 0 #000,
  2px -2px 0 #000,
  -2px 2px 0 #000,
  0px 2px 0 #000,
  2px 0px 0 #000,
  0px -2px 0 #000,
  -2px 0px 0 #000,
  2px 2px 5px #000;
  position: absolute;
  text-align: center;
  width: 100%;
  color: white;
  margin: 0;
}

.memContent .topText {
  position: absolute;
  top: 5px;
}

.memContent .bottomText {
  position: absolute;
  bottom: 5px;
}

form {
  padding: 25px 0;
}

input {
  width: 100%;
  padding: 3px 2px;
}
